  color: white;
  font-weight: bold;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
}

.header h1{
  text-align: center;
  margin: 0px;
  -ms-flex-positive: 1;
      flex-grow: 1;
}

.header h1 a{
  color: inherit;
