.Menu-Container{
  width: 400px;
  border: 5px solid #3D2F2C;
  margin: auto;
  margin-top: 20px;
  padding: 5px;
  background-color: lightpink;
}


.Menu-Item-Container{
  padding: 5px;
  border-bottom: 1px dashed grey;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
}

.Menu-Item-Container:hover{
  background-color: lightblue;


}

.Item{
  -ms-flex-positive: 1;
  flex-grow: 1;
}

.item-title{
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
}

.item-descr{
  font-size: calc(100% - 4px);
}

.User-Buttons{
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
}

.User-Buttons button{
  background-color: grey;
  padding: 8px;
  height: 10px;
  width: 10px;
  margin-left: 5px;
}

.edit-button{
  background-image: url(data:image/png;base64,);
}

.add-button{
  background-image: url(data:image/png;base64,);
}

.remove-button{
  background-image: url(data:image/png;base64,);
}

.desc{
  font-size: 12px;
}



